/* ###### Navigation   ###### */

.nav-link {
  &:hover, &:focus, &:active {
    outline: none;
  }
}

.nav-pills .nav-link {
  color: #596882;

  &:hover, &:focus {
    color: #1c273c;
  }

  &.active {
    border-radius: 2px;
    background-color: $primary;

    &:hover, &:focus {
      color: $white;
    }
  }
}

.nav-dark .nav-link {
  color: #97a3b9;

  &:hover, &:focus {
    color: $white;
  }
}

.nav-tabs {
  border-bottom-width: 0;

  .nav-link {
    background-color: $white-3;
    border-width: 0;
    border-radius: 0;
    padding: 10px 15px;
    line-height: 1.428;
    color: #596882;

    &:hover, &:focus {
      background-color: $white-5 ;
      color: #1c273c;
    }

    + .nav-link {
      margin-left: 3px;
    }

    &.active {
      background-color: $white;
      color: #1c273c;
      font-weight: 500;
      letter-spacing: -.1px;
    }
  }
}

@media (min-width: 768px) {
  .nav-tabs .nav-link {
    padding: 10px 20px;
  }
}

@media (min-width: 768px) {
  .main-nav {
    align-items: center;
  }
}

.main-nav .nav-link {
  display: block;
  color: #596882;
  padding: 0;
  position: relative;
  line-height: normal;

  &:hover, &:focus {
    color: #1c273c;
  }

  + .nav-link {
    padding-top: 12px;
    margin-top: 12px;
    border-top: 1px dotted #97a3b9;
  }

  &.active {
    color: $primary;
  }
}

@media (min-width: 768px) {
  .main-nav .nav-link + .nav-link {
    padding-top: 0;
    margin-top: 0;
    border-top: 0;
    padding-right: 15px;
    margin-right: 15px;
    border-right: 1px dotted #97a3b9;
  }
}

.main-nav-column {
  flex-direction: column;

  .nav-link {
    padding: 0;
    height: 43px;
    color: #1c273c;
    display: flex;
    align-items: center;
    justify-content: flex-start;

    i {
      font-size: 20px;
      line-height: 0;
      width: 24px;
      margin-left: 12px;
      text-align: center;
      transition: all 0.2s ease-in-out;

      &:not([class*=' tx-']) {
        color: $secondary;
      }

      &.typcn {
        line-height: .9;
      }
    }

    span {
      font-weight: 400;
      font-size: 11px;
      color: #97a3b9;
      margin-right: auto;
    }

    &:hover, &:focus, &:hover i:not([class*=' tx-']), &:focus i:not([class*=' tx-']) {
      color: #1c273c;
    }

    &.active {
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 6px;
        bottom: 6px;
        left: -28px;
        width: 3px;
        background-color: $primary;
        border-radius: 3px;
        display: none;
      }

      color: $primary;

      &:hover, &:focus, i, &:hover i, &:focus i {
        color: $primary;
      }
    }
  }

  &.sm .nav-link {
    font-size: 0.875rem;
    font-weight: 400;
    padding: 10px 0;

    i {
      font-size: 21px;
    }
  }
}

@media (prefers-reduced-motion: reduce) {
  .main-nav-column .nav-link i {
    transition: none;
  }
}

.main-nav-dark .nav-link {
  color: $white-7;

  &:hover, &:focus {
    color: $white;
  }

  + .nav-link {
    border-color: #596882;
  }

  &.active {
    color: $primary;
  }
}

.main-nav-colored-bg .nav-link {
  + .nav-link {
    border-color: $white-4;
  }

  &.active {
    color: $white;
  }
}

.main-nav-line {
  position: relative;

  .nav-link {
    padding: 0;
    color: #596882;
    position: relative;

    &:hover, &:focus {
      color: #1c273c;
    }

    padding: 15px 8px 12px 5px;
    color: #37326d;
    position: relative;

    &.active {
      color: #1c273c;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: 2px;
        background-color: transparent;
      }
    }
  }

  &.main-nav-dark .nav-link {
    color: $white-7;

    &:hover, &:focus {
      color: $white;
    }

    &.active {
      color: $white;

      &::before {
        background-color: $white;
      }
    }
  }
}

@media (min-width: 768px) {
  .main-nav-line .nav-link.active::before {
    top: auto;
    bottom: -20px;
    left: 0;
    right: 0;
    height: 2px;
    width: auto;
  }
}

.main-nav-tabs {
  padding: 15px 15px 0;
  background-color: #e2e8f5;

  .lSSlideOuter {
    position: relative;
    padding-left: 32px;
    padding-right: 35px;
  }

  .lSSlideWrapper {
    overflow: visible;
  }

  .lSAction > a {
    display: block;
    height: 40px;
    top: 16px;
    opacity: 1;
    background-color: #b4bdce;
    background-image: none;

    &:hover, &:focus {
      background-color: #a5afc4;
    }

    &::before {
      font-family: 'Ionicons';
      font-size: 18px;
      position: absolute;
      top: -4px;
      left: 0;
      right: 0;
      bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &.lSPrev {
      left: -32px;

      &::before {
        content: '\f3cf';
      }
    }

    &.lSNext {
      right: -35px;

      &::before {
        content: '\f3d1';
      }
    }

    &.disabled {
      background-color: #f6f9fd;
      color: $white;
    }
  }

  .lightSlider {
    display: flex;
  }

  .tab-item {
    flex-shrink: 0;
    display: block;
    float: none;
    min-width: 150px;
  }

  .tab-link {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 20px;
    line-height: 1.428;
    color: #596882;
    white-space: nowrap;
    background-color: #f6f9fd;

    &:hover, &:focus {
      background-color: #f6f9fd;
    }

    &.active {
      background-color: $white;
      color: #1c273c;
      font-weight: 500;
    }
  }
}

.main-tab-pane {
  display: none;

  &.active {
    display: block;
  }
}

