.navbar-light {
  .navbar-brand {
    color: $black-9;

    &:hover, &:focus {
      color: $black-9;
    }
  }

  .navbar-nav {
    .nav-link {
      color: $black-5;

      &:hover, &:focus {
        color: $black-7;
      }

      &.disabled {
        color: $black-3;
      }
    }

    .show > .nav-link, .active > .nav-link {
      color: $black-9;
    }

    .nav-link {
      &.show, &.active {
        color: $black-9;
      }
    }
  }

  .navbar-toggler {
    color: $black-5;
    border-color: $black-1;
  }

  .navbar-toggler-icon {
    background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='$black-5' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
  }

  .navbar-text {
    color: $black-5;

    a {
      color: $black-9;

      &:hover, &:focus {
        color: $black-9;
      }
    }
  }
}

.navbar-dark {
